"use client";

import { useState, useEffect, useRef } from "react";
import { useRouter } from "next/navigation";
import { motion, AnimatePresence } from "framer-motion";
import { Search, X, Command, ArrowRight, Loader2, TrendingUp, Clock } from "lucide-react";
import { cn } from "@/lib/utils";

// --- Mock Data for Demonstration ---
const MOCK_PRODUCTS = [
  { id: 1, name: "Gold Bangle Set", category: "Bangles", slug: "gold-bangle-set", image: "/placeholder.jpg" },
  { id: 2, name: "Diamond Solitaire Ring", category: "Rings", slug: "diamond-ring", image: "/placeholder.jpg" },
  { id: 3, name: "Silver Anklet", category: "Anklets", slug: "silver-anklet", image: "/placeholder.jpg" },
  { id: 4, name: "Bridal Chura", category: "Wedding", slug: "bridal-chura", image: "/placeholder.jpg" },
  { id: 5, name: "Rose Gold Bracelet", category: "Bracelets", slug: "rose-gold-bracelet", image: "/placeholder.jpg" },
];

export default function SearchBar() {
  const [isOpen, setIsOpen] = useState(false);
  const [query, setQuery] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [results, setResults] = useState<typeof MOCK_PRODUCTS>([]);
  const inputRef = useRef<HTMLInputElement>(null);
  const router = useRouter();

  // Handle Keyboard Shortcut (Cmd+K)
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if ((e.metaKey || e.ctrlKey) && e.key === "k") {
        e.preventDefault();
        setIsOpen((prev) => !prev);
      }
      if (e.key === "Escape") {
        setIsOpen(false);
      }
    };
    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, []);

  // Focus input when opened
  useEffect(() => {
    if (isOpen) {
      setTimeout(() => inputRef.current?.focus(), 100);
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "unset";
    }
  }, [isOpen]);

  // Handle Search Logic (Debounced Mock)
  useEffect(() => {
    if (!query) {
      setResults([]);
      return;
    }

    const delayDebounceFn = setTimeout(() => {
      setIsLoading(true);
      // Simulate API call
      setTimeout(() => {
        const filtered = MOCK_PRODUCTS.filter((item) =>
          item.name.toLowerCase().includes(query.toLowerCase()) ||
          item.category.toLowerCase().includes(query.toLowerCase())
        );
        setResults(filtered);
        setIsLoading(false);
      }, 500); // 500ms fake network delay
    }, 300); // 300ms typing debounce

    return () => clearTimeout(delayDebounceFn);
  }, [query]);

  const handleNavigate = (slug: string) => {
    setIsOpen(false);
    router.push(`/shop/${slug}`);
  };

  return (
    <>
      {/* --- TRIGGER BUTTON (Visible in Navbar) --- */}
      <button
        onClick={() => setIsOpen(true)}
        className="group relative flex items-center gap-2 w-full bg-muted/50 hover:bg-muted border border-transparent hover:border-border/50 rounded-full px-3 py-2 transition-all duration-200"
      >
        <Search className="w-4 h-4 text-muted-foreground group-hover:text-primary transition-colors" />
        <span className="text-sm text-muted-foreground group-hover:text-foreground text-left flex-1 truncate">
          Search...
        </span>
        <kbd className="hidden md:inline-flex h-5 select-none items-center gap-1 rounded border bg-muted px-1.5 font-mono text-[10px] font-medium text-muted-foreground opacity-100">
          <span className="text-xs">⌘</span>K
        </kbd>
      </button>

      {/* --- FULL SCREEN OVERLAY --- */}
      <AnimatePresence>
        {isOpen && (
          <div className="fixed inset-0 z-[60] flex items-start justify-center pt-[15vh] px-4">
            
            {/* Backdrop */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              onClick={() => setIsOpen(false)}
              className="absolute inset-0 bg-background/80 backdrop-blur-sm"
            />

            {/* Modal Content */}
            <motion.div
              initial={{ opacity: 0, scale: 0.95, y: -20 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.95, y: -20 }}
              transition={{ duration: 0.2 }}
              className="relative w-full max-w-2xl bg-background border border-border shadow-2xl rounded-2xl overflow-hidden flex flex-col max-h-[60vh]"
            >
              {/* Header / Input Area */}
              <div className="flex items-center px-4 py-4 border-b border-border/50 gap-3">
                <Search className="w-5 h-5 text-muted-foreground" />
                <input
                  ref={inputRef}
                  value={query}
                  onChange={(e) => setQuery(e.target.value)}
                  placeholder="What are you looking for?"
                  className="flex-1 bg-transparent border-none outline-none text-lg placeholder:text-muted-foreground/50 h-full"
                />
                <button 
                  onClick={() => setIsOpen(false)}
                  className="p-1 hover:bg-muted rounded-md transition-colors"
                >
                  <span className="sr-only">Close</span>
                  <div className="text-xs text-muted-foreground border border-border rounded px-1.5 py-0.5">ESC</div>
                </button>
              </div>

              {/* Results Area */}
              <div className="flex-1 overflow-y-auto p-2">
                
                {/* STATE: Loading */}
                {isLoading && (
                  <div className="py-12 flex flex-col items-center justify-center text-muted-foreground gap-2">
                    <Loader2 className="w-6 h-6 animate-spin text-primary" />
                    <span className="text-sm">Searching craftsmanship...</span>
                  </div>
                )}

                {/* STATE: Empty Query (Show Suggestions) */}
                {!query && !isLoading && (
                  <div className="p-2">
                    <div className="text-xs font-semibold text-muted-foreground mb-3 px-2 flex items-center gap-2">
                      <TrendingUp className="w-3 h-3" /> TRENDING NOW
                    </div>
                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
                       {['Bridal Sets', 'Diamond Rings', 'Gold Coins', 'Gift Cards'].map((item) => (
                         <button 
                            key={item} 
                            onClick={() => setQuery(item)}
                            className="text-left px-3 py-2 rounded-lg hover:bg-muted/50 text-sm transition-colors flex items-center justify-between group"
                         >
                            {item}
                            <ArrowRight className="w-3 h-3 opacity-0 group-hover:opacity-100 -translate-x-2 group-hover:translate-x-0 transition-all" />
                         </button>
                       ))}
                    </div>
                  </div>
                )}

                {/* STATE: No Results */}
                {query && !isLoading && results.length === 0 && (
                  <div className="py-12 text-center text-muted-foreground">
                    <p>No results found for <span className="text-foreground font-medium">"{query}"</span></p>
                    <p className="text-sm mt-1">Try searching for "Gold" or "Rings"</p>
                  </div>
                )}

                {/* STATE: Results Found */}
                {results.length > 0 && !isLoading && (
                  <div className="space-y-1">
                    <div className="text-xs font-semibold text-muted-foreground mb-2 px-2 mt-2">PRODUCTS</div>
                    {results.map((product) => (
                      <motion.button
                        key={product.id}
                        layout
                        onClick={() => handleNavigate(product.slug)}
                        className="w-full flex items-center gap-4 p-2 rounded-lg hover:bg-muted transition-colors group text-left"
                      >
                        <div className="w-10 h-10 rounded-md bg-muted/50 overflow-hidden relative">
                           {/* Replace with Next/Image in real usage */}
                           <div className="absolute inset-0 bg-gradient-to-tr from-gray-200 to-gray-100" />
                        </div>
                        <div className="flex-1">
                          <h4 className="text-sm font-medium text-foreground group-hover:text-primary transition-colors">
                            {product.name}
                          </h4>
                          <p className="text-xs text-muted-foreground">{product.category}</p>
                        </div>
                        <ArrowRight className="w-4 h-4 text-muted-foreground opacity-0 group-hover:opacity-100 -translate-x-2 group-hover:translate-x-0 transition-all" />
                      </motion.button>
                    ))}
                  </div>
                )}
              </div>

              {/* Footer */}
              <div className="p-3 border-t border-border/50 bg-muted/20 text-[10px] text-muted-foreground flex justify-between items-center">
                 <span>Search by Banglerd</span>
                 <div className="flex gap-3">
                    <span className="flex items-center gap-1"><ArrowRight className="w-3 h-3" /> to select</span>
                    <span className="flex items-center gap-1"><ArrowRight className="w-3 h-3 rotate-90" /> to navigate</span>
                 </div>
              </div>
            </motion.div>
          </div>
        )}
      </AnimatePresence>
    </>
  );
}